.software-flex {
  display: flex;
  justify-content: space-between;
  align-content: flex-start;
  flex-wrap: wrap;
  height: 28rem;
  margin-left: .7rem;
  margin-right: .3rem;

  @media screen and (max-width: 528px) {
    display: block;
  }
}

.software {
  &__item {
    flex-basis: 44.2%;
    margin-bottom: .8rem;

    @media screen and (max-width: 528px) {
      width: 100%;
    }
  }

  &__name {
    font-size: 2.5rem;
    margin-top: 1.17rem;
    margin-bottom: 1.05rem;
    letter-spacing: .02rem;
    text-transform: uppercase;
  }

  &__level {
    height: 2.1rem;
    background-color: $color-dots;

    span {
      display: inline-block;
      height: 100%;
      transition: width .4s ease-in-out;
      background-color: $color-main;
      background-size: 40px 40px;
      @include animation(webkit);
      @include animation(moz);
      @include linear-gradient($color-gradient, 25%);
    }
  }
}